import React, { useState } from "react"
import { Payments, Modal, Empty, Section, Button, Heading, ButtonLink } from "components"
import { Formik } from "formik"
import Script from "react-load-script"
import { Form, Row, Col, List, Typography, Icon, message, TimePicker } from "antd"
import TextInput from "components/forms/TextInput"
import SelectInput from "components/forms/SelectInput"
import { SubmitButton } from "formik-antd"
import TimeInput from "components/forms/TimeInput"
import { useMutation } from "react-apollo"
import { newClient, googleApiKey } from "helpers"
import ApiCal from "react-google-calendar-api"
import moment from "moment"
import { withI18n } from "@lingui/react"
import { Trans, t } from "@lingui/macro"
import { gapi } from "gapi-script"
import { ADD } from "queries/booking"

const DateModal = ({ i18n, unauthorizedUser, userDetails, data, onCancel, visible, userData, onAdded }) => {
	let autocomplete
	const isStudent = userDetails.token && userDetails.role.code === "STDN" ? true : false
	const [bookingData, setBookingData] = useState({})
	const [isAdding, setIsAdding] = useState(false)
	const [showPayment, setShowPayment] = useState(false)
	const subjectOptions = (userData.subjectList || []).map((s) => ({
		value: s.subject.pvid,
		label: `${s.subject.name}`
		// label: `${s.subject.name} - ${s.subject.level[0].name}`
	}))
	const durationOptions = [
		{ value: "0:30", label: <Trans>30 Minutes</Trans> },
		{ value: "1:0", label: <Trans>1 Hour</Trans> },
		{ value: "1:30", label: <Trans>1 Hour 30 Minutes</Trans> },
		{ value: "2:0", label: <Trans>2 Hour</Trans> },
		{ value: "2:30", label: <Trans>2 Hour 30 Minutes</Trans> },
		{ value: "3:0", label: <Trans>3 Hour</Trans> },
		{ value: "3:30", label: <Trans>3 Hour 30 Minutes</Trans> },
		{ value: "4:0", label: <Trans>4 Hour</Trans> },
		{ value: "4:30", label: <Trans>4 Hour 30 Minutes</Trans> },
		{ value: "5:0", label: <Trans>5 Hour</Trans> },
		{ value: "5:30", label: <Trans>5 Hour 30 Minutes</Trans> },
		{ value: "6:0", label: <Trans>6 Hour</Trans> }
	]

	const [location, setLocation] = useState("")

	const { date, schedule } = data

	const [addBooking] = useMutation(ADD, {
		client: newClient("booking"),
		onCompleted: (data) => {
			if (data.addBooking) {
				message.success(i18n._(t`Successfully add booking`))
				setBookingData(data.addBooking)
				setIsAdding(false)
				setShowPayment(true)
				onAdded()
			}
		},
		onError: (err) => {
			const error = (err.graphQLErrors || [])[0] || {}
			message.error(i18n._(t`${error}`))
		}
	})

	const handleAddNewSchedule = async (input, { setSubmitting }) => {
		try {
			const { duration, ...values } = input
			const mStart = moment(values.startTime)
			const startTime = date.hours(mStart.hours()).minutes(mStart.minutes())
			const hour = duration.split(":")[0]
			const minute = duration.split(":")[1]

			values.startTime = startTime.toISOString()
			values.endTime = startTime.add(hour, "hours").add(minute, "minutes").toISOString()

			const subjectInfo = (userData.subjectList || []).find((s) => s.pvid === values.subjectPvid)
			const fix_duration = Number(hour) + Number(minute) / 60
			const bookingData = {
				...values,
				location,
				tutorToken: userData.userCode,
				price: subjectInfo.pricePerHour * fix_duration,
				duration: fix_duration
			}
			await addBooking({ variables: { ...bookingData, status: "pending" } })
			setSubmitting(false)
			// bookingData.duration = { hour, minute }
			// setBookingData(bookingData)
			// setIsAdding(false)
			// setShowPayment(true)
			// onAdded()
		} catch (error) {
			message.error(i18n._(t`${error}`))
		}
	}

	const handlePaymentClose = () => {
		onCancel()
		setShowPayment(false)
		setBookingData({})
	}

	const handleDeleteEvent = (eventId) => {
		gapi.client.calendar.events
			.delete({
				calendarId: "primary",
				eventId
			})
			.then(() => {
				onCancel()
				message.success("It's been deleted!")
			})
	}

	const handleListEvents = () => {
		if (ApiCal.sign) {
			ApiCal.listUpcomingEvents(10).then(({ result }) => console.log({ cal: result.items }))
		}
	}

	const handleLoad = () => {
		const options = {
			types: [],
			componentRestrictions: { country: userDetails.country }
		}

		/*global google*/
		autocomplete = new google.maps.places.Autocomplete(document.getElementById("location"), options)
		autocomplete.addListener("place_changed", handlePlaceSelect)
	}

	const handlePlaceSelect = () => {
		const addressObject = autocomplete.getPlace()
		const userAddress = addressObject.address_components

		if (userAddress) {
			setLocation(addressObject.formatted_address)
		}
	}

	const renderModal = () => {
		if (showPayment && isStudent) {
			return <Payments userDetails={userDetails} bookingData={bookingData} userData={userData} handleClose={handlePaymentClose} />
		}

		if (isAdding) {
			return (
				<Formik
					onSubmit={handleAddNewSchedule}
					initialValues={{ subjectPvid: "" }}
					validate={(values) => {
						const errors = {}
						if (!values.subjectPvid) errors.title = "This is required"
						if (!values.startTime) errors.startTime = "This is required"
						if (!values.duration) errors.duration = "This is required"
						return errors
					}}
				>
					{({ errors, handleChange, handleSubmit }) => (
						<Form layout="vertical" onSubmit={handleSubmit} className="pb2em">
							<SelectInput
								name="subjectPvid"
								label={<Trans>Subject</Trans>}
								placeholder={<Trans>Select Subject</Trans>}
								options={subjectOptions}
							/>
							<Script url={googleApiKey} onLoad={handleLoad} />
							<TextInput
								special
								allowClear
								id="location"
								name="location"
								placeholder={i18n._(t`Your location...`)}
								label={<Trans>Your location</Trans>}
								value={location}
								helpText={<Trans>Start typing some characters to show place options</Trans>}
								onChange={(e) => setLocation(e.target.value)}
							/>
							<Row gutter={16}>
								<Col lg={12}>
									<TimeInput
										name="startTime"
										placeholder="Select time"
										label={<Trans>Start Time</Trans>}
										minuteStep={15}
										format="HH:mm"
									/>
								</Col>
								<Col lg={12}>
									<SelectInput
										name="duration"
										label={<Trans>Duration</Trans>}
										placeholder={<Trans>Select Duration</Trans>}
										options={durationOptions}
									/>
									{/* <TimeInput
										name="endTime"
										placeholder="What time is the event ends"
										label="End time"
										minuteStep={15}
										format="HH:mm"
									/> */}
								</Col>
							</Row>
							<SubmitButton type="primary" disabled={Object.keys(errors).length > 0 || !location}>
								Submit
							</SubmitButton>{" "}
							&nbsp; <ButtonLink onClick={() => setIsAdding(false)}>Cancel</ButtonLink>
						</Form>
					)}
				</Formik>
			)
		}

		if (isStudent) {
			return (
				<div>
					{(data.schedule || []).length === 0 && !isAdding ? (
						<Section centered>
							<Empty className="mt0" description={<Trans>No schedule in this date</Trans>} />
						</Section>
					) : (
						<List
							dataSource={schedule}
							itemLayout="horizontal"
							renderItem={(item) => (
								<List.Item>
									<List.Item.Meta
										title={item.title}
										description={
											<>
												<Heading
													level={4}
													marginBottom="2em"
													subheader={
														<Typography>
															{/* <Typography.Paragraph className="mb2em grey">{item.notes}</Typography.Paragraph> */}
															<Typography.Paragraph>
																<Icon type="clock-circle" /> &nbsp;
																{moment(item.startTime).format("hh:mm A")} -{" "}
																{moment(item.endTime).format("hh:mm A")}
															</Typography.Paragraph>
															<Typography.Paragraph>
																<Icon type="pushpin" /> &nbsp;
																{item.location}
															</Typography.Paragraph>
														</Typography>
													}
												/>
											</>
										}
									/>
								</List.Item>
							)}
						/>
					)}
					<Section centered>
						<Button type="primary" icon="plus" onClick={() => setIsAdding(true)}>
							&nbsp; <Trans>Book this date</Trans>
						</Button>
					</Section>
				</div>
			)
		} else {
			return (
				<div>
					{(data.schedule || []).length === 0 ? (
						<Section centered>
							<Empty className="mt0" description={<Trans>No schedule in this date</Trans>} />
						</Section>
					) : (
						<List
							dataSource={schedule}
							itemLayout="horizontal"
							renderItem={(item) => (
								<List.Item>
									<List.Item.Meta
										title={item.title}
										description={
											<>
												<Heading
													level={4}
													marginBottom="2em"
													subheader={
														<Typography>
															{/* <Typography.Paragraph className="mb2em grey">{item.notes}</Typography.Paragraph> */}
															<Typography.Paragraph>
																<Icon type="clock-circle" /> &nbsp;
																{moment(item.startTime).format("hh:mm A")} -{" "}
																{moment(item.endTime).format("hh:mm A")}
															</Typography.Paragraph>
															<Typography.Paragraph>
																<Icon type="pushpin" /> &nbsp;
																{item.location}
															</Typography.Paragraph>
														</Typography>
													}
												/>
												{/* {!unauthorizedUser && (
													<Button type="primary" icon="close" onClick={() => handleDeleteEvent(item.id)}>
														&nbsp; <Trans>Delete this event</Trans>
													</Button>
												)} */}
											</>
										}
									/>
								</List.Item>
							)}
						/>
					)}
				</div>
			)
		}
	}

	const handle_cancel = () => {
		onCancel()
		setShowPayment(false)
		setIsAdding(false)
		setBookingData({})
	}

	return (
		<Modal
			width="570px"
			visible={visible}
			onCancel={handle_cancel}
			title={
				<Heading
					marginBottom="0"
					reverse
					level={4}
					content={<Trans>Schedule</Trans>}
					subheader={date && date.format("dddd, DD MMM YYYY")}
				/>
			}
			footer={false}
		>
			{renderModal()}
		</Modal>
	)
}

export default withI18n()(DateModal)
